import React, {useState, useEffect} from 'react';
// import PropTypes from 'prop-types';
import Slide from './Slide';
import SliderLeftArrow from './SliderLeftArrow';
import SliderRightArrow from './SliderRightArrow';
import Dots from './Dots';

import '../scss/Slider.scss';
import '../scss/button.scss';


const Slider = (props) => {

  const [index, setIndex] = useState(0);
  const [translateValue, setTranslateValue] = useState(0);

  useEffect(() => {
    slideWidth();
  });

  const renderSlides = () => {
    let slides = [];
    for (let i = 0; i < imagesObj.length; i++) {
      slides.push(
        <Slide key={i} image={imagesObj[i]}/>
      );
    }
    return slides;
  };
  const slideWidth = () => {
    const slide = document.querySelector('.slide');
    return slide.clientWidth
  };

  const goToPreviousSlide = () => {
    if (index === 0) {
      setIndex(imagesObj.length - 1);
      setTranslateValue(translateValue - slideWidth() * (imagesObj.length - 1));
      return;
    }

    setTranslateValue(translateValue + slideWidth());
    setIndex(index - 1);
  };

  const goToNextSlide = () => {
    if (index === imagesObj.length - 1) {
      setTranslateValue(0);
      setIndex(0);
      return;
    }

    setTranslateValue(translateValue - slideWidth());
    setIndex(index + 1);
  };

  const handleDotClick = (i) => {
    if (i === index)
      return;

    if (i > index) {
      setTranslateValue(-(i * slideWidth()));
      setIndex(i);
    }
    else {
      setTranslateValue(translateValue + ((index - i) * (slideWidth())));
      setIndex(i);
    }
  };

  return (
    <div className="slider">

      <div className="slider-wrapper" style={{
        transform: `translateX(${translateValue}px)`,
        transition: 'transform ease-out 0.35s'
      }}>
        {renderSlides()}
      </div>

      <SliderLeftArrow prevSlide={goToPreviousSlide}/>
      <SliderRightArrow nextSlide={goToNextSlide}/>

      <Dots
        index={index}
        quantity={imagesObj.length}
        dotClick={handleDotClick}
        images={imagesObj}
      />

      <div className="header">
        <div className="logo">
          <img src={require('../img/logos-waxom.png')} alt="logo"/>
          <p>Waxom</p>
        </div>
        <ul className="menu">
          <li className="item"><a href="#">Home</a></li>
          <li className="item"><a href="#">About Us</a></li>
          <li className="item"><a href="#">Portfolio</a></li>
          <li className="item"><a href="#">Features</a></li>
          <li className="item"><a href="#">Blog</a></li>
          <li className="item"><a href="#">Pricing</a></li>
          <li className="item"><a href="#">Shtortcodes</a></li>
          <li className="item"><a href="#">Contact</a></li>
        </ul>
        <div className="search">
          <img src={require('../img/icon-cart.png')} alt="biba"/>
          <img src={require('../img/icon-search.png')} alt="search"/>
        </div>
      </div>

      <div className="content">
        <h3>Unique and Modern Design</h3>
        <h1>Portfolio PSD Template</h1>
        <div>Nam liber tempor cum soluta nobis eleifend option congue nihil imperdiet doming id quod mazim placerat facer
          possim assu
        </div>
        <a className="btn btn-main" href="#">Get Started</a>
      </div>

    </div>
  );
};

// Slider.propTypes = {};

export default Slider;

const imagesObj = [
  {
    img: require('../img/img3.jpg')
  },
  {
    img: require('../img/img1.jpg')
  }/*,
  {
    img: require('../img/img2.jpg')
  }*/,
  {
    img: require('../img/img4.jpg')
  },
  {
    img: require('../img/img5.jpg')
  }
];