import React from 'react';

import "../scss/Logos.scss";

const Logos = (props) => {
  return (
    <div className="logos">
      {/*<div className="wrapper">*/}
        <div>
          <img src={require('../img/logo-01.png')} alt="logo"/>
        </div>
        <div>
          <img src={require('../img/logo-02.png')} alt="logo"/>
        </div>
        <div>
          <img src={require('../img/logo-03.png')} alt="logo"/>
        </div>
        <div>
          <img src={require('../img/logo-04.png')} alt="logo"/>
        </div>
      {/*</div>*/}
    </div>
  );
};

export default Logos;