import React, {Fragment} from 'react'
import Dot from './Dot'

const Dots = ({index, quantity, dotClick, images}) => {

  let dots = [];

  for (let i = 0; i < quantity; i++) {
    let isActive = i === index;

    dots.push(
      <Dot key={i}
           id={i}
           active={isActive}
           dotClick={dotClick}
      />
    )
  }

  return (
    <div className="dots-container">
      {dots}

     {/* {
        images.map( (item, i) => {
          let isActive = i === index;
          return <Dot key={i}
               id={i}
               active={isActive}
               dotClick={dotClick}
          />
        })
      }*/}
    </div>
  )
}

export default Dots
